:root {
  --color-text: #333;
  --color-bg: #fff;
  --color-surface: #f9f9f9;
  --color-primary: #333;
  --color-primary-hover: #000;
  --color-border: #0000001a;
  --color-success: #4caf50;
  --color-warning: #ff9800;
  --color-danger: #f44336;
  --radius-sm: 4px;
  --radius-md: 8px;
  --radius-lg: 12px;
  --shadow-sm: 0 2px 6px #0000000d;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: inherit;
}

body {
  background-color: var(--color-bg);
  color: var(--color-text);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
  margin: 0;
  font-family: sans-serif;
  line-height: 1.5;
  display: flex;
  overflow-x: hidden;
}

header, #game-container, #result-display {
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
}

#game-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  width: 100%;
  max-width: 800px;
  padding: 1rem;
  display: flex;
}

#game-stats {
  flex-wrap: wrap;
  justify-content: center;
  gap: .5rem;
  width: 100%;
  display: flex;
}

#game-progress-container {
  text-align: center;
  flex: 0 0 100%;
  order: -1;
  align-self: center;
  padding: .5rem;
}

.stats {
  flex-direction: column;
  flex: 40%;
  gap: .5rem;
  min-width: 0;
  padding: .5rem 0;
  display: flex;
}

.score-row {
  font-weight: bold;
}

.bar-wrapper {
  border: 2px solid var(--color-text);
  border-radius: var(--radius-sm);
  background-color: var(--color-danger);
  width: 300px;
  max-width: 100%;
  height: 30px;
  position: relative;
  overflow: hidden;
  box-shadow: inset 0 2px 4px #00000040, inset 0 -4px 6px #0a0a0a33;
}

.bar {
  background-color: var(--color-success);
  width: 100%;
  height: 100%;
  transition: width .6s cubic-bezier(.175, .885, .32, 1.275);
  position: absolute;
  top: 0;
  left: 0;
}

.bar-text {
  color: #fff;
  z-index: 5;
  text-shadow: 1px 1px 2px #000;
  white-space: nowrap;
  pointer-events: none;
  font-size: 1.1rem;
  font-weight: bold;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

#player-stats {
  text-align: left;
  align-items: flex-start;
}

#player-stats .bar-wrapper {
  justify-content: flex-start;
}

#player-stats .bar-text {
  left: 10px;
}

#computer-stats {
  text-align: right;
  align-items: flex-end;
}

#computer-health {
  left: auto;
  right: 0;
}

#computer-stats .bar-text {
  right: 10px;
}

@media (width >= 768px) {
  #game-stats {
    grid-template-columns: 1fr auto 1fr;
    align-items: end;
    gap: 0;
    display: grid;
  }

  #game-progress-container {
    order: 0;
    min-width: 180px;
    padding: 0 2rem;
  }

  .stats {
    width: auto;
  }

  #player-stats {
    text-align: left;
    align-items: flex-start;
  }

  #computer-stats {
    text-align: right;
    align-items: flex-end;
  }
}

#choices, #move-reveal {
  flex-wrap: nowrap;
  justify-content: center;
  align-items: stretch;
  gap: .5rem;
  width: 100%;
  display: flex;
}

.card-container {
  aspect-ratio: 2 / 3;
  border: 1px solid var(--color-border);
  border-radius: var(--radius-lg);
  background-color: var(--color-surface);
  min-width: 0;
  max-width: 120px;
  box-shadow: var(--shadow-sm);
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
  gap: .25rem;
  padding: .5rem;
  display: flex;
}

button.card-container {
  cursor: pointer;
  border: 1px solid var(--color-border);
  background: var(--color-surface);
  transition: all .2s;
}

button.card-container:not(:disabled):hover {
  background-color: #fff;
  border-color: #0000004d;
  transform: translateY(-5px);
  box-shadow: 0 8px 20px #0000001a;
}

button.card-container:disabled {
  opacity: .4;
  cursor: not-allowed;
  filter: grayscale();
}

.icon {
  filter: drop-shadow(0 4px 4px #0000001a);
  font-size: 2.5rem;
  line-height: 1;
}

.label {
  color: #666;
  text-transform: uppercase;
  letter-spacing: .5px;
  font-size: .7rem;
  font-weight: 700;
}

.vs-label {
  color: #ddd;
  align-self: center;
  font-size: 1.5rem;
  font-style: italic;
  font-weight: 900;
}

@media (width >= 768px) {
  #choices, #move-reveal {
    gap: 1.5rem;
  }

  .icon {
    font-size: 4rem;
  }

  .label {
    font-size: .9rem;
  }
}

.hidden {
  display: none !important;
}

.visually-hidden {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

#main-menu {
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 50vh;
  display: flex;
}

.menu-content {
  text-align: center;
  flex-direction: column;
  gap: 2.5rem;
  width: 100%;
  max-width: 400px;
  padding: 2rem;
  display: flex;
}

.title-large {
  color: var(--color-primary);
  text-transform: uppercase;
  letter-spacing: -1px;
  font-size: 3rem;
  font-weight: 800;
  line-height: 1.1;
}

.menu-controls {
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  display: flex;
}

button {
  font-family: inherit;
  font-size: 1rem;
}

.btn-primary, .btn-secondary, #play-again {
  border-radius: var(--radius-md);
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: .5px;
  padding: .85rem 1.5rem;
  font-weight: 700;
  transition: all .2s;
}

.btn-primary, #play-again {
  background-color: var(--color-primary);
  color: #fff;
  border: 2px solid var(--color-primary);
}

.btn-primary:hover, #play-again:hover {
  background-color: var(--color-primary-hover);
  border-color: var(--color-primary-hover);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px #00000026;
}

.btn-secondary {
  color: var(--color-text);
  border: 2px solid var(--color-border);
  background-color: #0000;
}

.btn-secondary:hover {
  border-color: var(--color-primary);
  background-color: #fff;
}

#arena {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 100%;
  min-height: 200px;
  display: flex;
}

#announcement-container {
  text-align: center;
  min-height: 2.5rem;
}

#announcement-container h2 {
  margin: 0;
  font-size: 1.5rem;
}

#game-controls {
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
}

#choices, #progression-zone {
  justify-content: center;
  width: 100%;
  display: flex;
}
